<template>
  <div class="weather-wapper">
    <mt-header title="">
      <router-link to="/home/weather" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
    </mt-header>
    <div class="forecast-info">
      <table>
        <thead>
        <tr>
          <th v-for="item in titleList">{{item}}</th>
          <th>
            <div class="last-th"></div>
          </th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="item in list">
          <td>{{item.col1}}</td>
          <td :class="[item.col2==''?'yellow':'blue']">{{item.col2}}</td>
          <td :class="[item.col3==''?'yellow':'blue']">{{item.col3}}</td>
          <td :class="[item.col4==''?'yellow':'blue']">{{item.col4}}</td>
          <td :class="[item.col5==''?'yellow':'blue']">{{item.col5}}</td>
          <td :class="[item.col6==''?'yellow':'blue']">{{item.col6}}</td>
          <td :class="[item.col7==''?'yellow':'blue']">{{item.col7}}</td>
        </tr>
        </tbody>
      </table>
    </div>

  </div>
</template>

<script>
  export default {
    name: "weather",
    data() {
      return {
        titleList: ['', '', '', '', '', ''],
        list: [
          {col1: '8', col2: '', col3: '-', col4: '', col5: '', col6: '', col7: ''},
          {col1: '9', col2: '', col3: '', col4: '-', col5: '', col6: '', col7: ''},
          {col1: '10', col2: '', col3: '', col4: '', col5: '', col6: '', col7: '-'},
          {col1: '11', col2: '', col3: '', col4: '', col5: '-', col6: '', col7: ''},
          {col1: '12', col2: '', col3: '', col4: '-', col5: '', col6: '-', col7: ''},
          {col1: '13', col2: '-', col3: '', col4: '', col5: '-', col6: '', col7: ''},
          {col1: '14', col2: '-', col3: '', col4: '', col5: '', col6: '-', col7: ''}
        ]
      }
    },
    components: {}
  }
</script>

<style lang="scss" scoped>
  .weather-wapper {
    /*margin-top:1.2rem;*/
    padding-top: 1.2rem;
    color: #fff;
    .forecast-info {
      padding: 30px;
      width: 690px;
      table {
        width: 100%;
        margin-top: 36px;
        width: 100%;
      }
      table tr {
        border-bottom: solid 3px #29374a;
      }
      thead {
        background: #005853;
      }
      tbody tr {
        height: 98px;
        line-height: 98px;
        text-align: center;
      }
      tr th {
        height: 80px;
        line-height: 90px;
        font-size: 26px;
      }
      .last-th {
        width: 60px;
        line-height: 30px;
        display: inline-block;
        box-sizing: border-box;
        transform: translateY(16px);
      }
      table tbody tr td:nth-child(1) {
        color: #cfe2ff;
      }
      table tbody tr td:nth-child(odd) {
        background: #092223;
      }
      .blue {
        color: #38f2ec;
      }
      .yellow {
        color: #ffe328;
      }
    }
  }

</style>


